@import url(https://fonts.googleapis.com/css2?family=Allerta+Stencil&display=swap);
@import url(https://cdn.jsdelivr.net/gh/tretapey/raisincss@1.1.0/raisin.min.css);
@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cinzel&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}



.nameFont {
  font-family: 'Fjalla One', sans-serif;
}

.textFont {
  font-family: 'Roboto', sans-serif;
}

.Title{
  width: 100%;

}

.body{
  border: 2px solid blue;
  height:100%;
  width: 100%;
  background-color: #282c34;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
}
.left{
  width: 30%;
  height: 40%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.wrapper {

  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.right{
  height: 100%;
  width: 70%;
  background-color: purple;
}


.neonpurple {
  text-transform: uppercase;

  font-weight: bold;
  font-style: italic;
  letter-spacing: .1em;
  color: white;
  text-shadow: 0 0 5px white,
  0 0 10px white,
  0 0 20px purple,
  0 0 40px purple,
  0 0 60px purple,
  0 0 90px purple,
  0 0 120px purple,
  0 0 160px purple;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  font-family: 'Allerta Stencil', sans-serif;
  background-color: #282c34;
  min-height: 100vh;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.gFont1 {
  font-family: 'Cinzel', serif;
}

.button1 {
  color: white;
  background-color: black;
  border: 1px solid black;
  border-radius: 4px;
  padding: 0 15px;
  cursor: pointer;
  height:80px;
  transition: all 0.2s ease-in-out;
  opacity: 0.5;
}
.button2:hover {
  -webkit-animation: spin 2s ease-in-out .5s infinite alternate;
          animation: spin 2s ease-in-out .5s infinite alternate;
}

@-webkit-keyframes spin {
  from{
    transform: rotateZ(0deg);
  }
  to{
    transform: rotateZ(360deg);
  }
}

@keyframes spin {
  from{
    transform: rotateZ(0deg);
  }
  to{
    transform: rotateZ(360deg);
  }
}

/* The line */
.vertical-timeline::before {
  background: #013535 !important;
  opacity: 0.4;
  border-radius: 50%;
}



:focus{
    outline: none;
}
.input-container{
    border: 3px solid black;




    float: left;
    width: 27.33%;
    margin: 42px 3%;
    position: relative;
}
input[type="text"]{
    color: #1d9af2;
    width: 70%;
    box-sizing: border-box;
    letter-spacing: 1px;
    background-color: rgb(19, 23, 44);
    border: none;
}
.effect-1~.focus-border{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 2px;
    background-color: #3399FF;
    transition: 0.4s;
}
.effect-1:focus~.focus-border {
    width: 70%;
    transition: 0.4s;
}
.noDotList {
    list-style: none;
}


.neonred {
    text-transform: uppercase;
    font-size: 60px;
    font-weight: bold;
    font-style: italic;
    letter-spacing: .1em;
    color: white;
    text-shadow: 0 0 5px white,
    0 0 10px white,
    0 0 20px red,
    0 0 40px red,
    0 0 60px red,
    0 0 90px red,
    0 0 120px red,
    0 0 160px red;
}

